
    .user-profile {
        position: relative;
        display: inline-block;
        margin-right: 20px;
    }

    .user-options {
        position: absolute;
        top: 100%;
        right: 0;
        background-color: #fff;
        border: 1px solid #ccc;
        padding: 10px;
        border-radius: 5px;
        box-shadow: 0 2px 5px rgba(0,0,0,0.1);
        display: none;
    }

    .user-profile:hover .user-options {
        display: block;
    }

    .user-option {
        cursor: pointer;
        padding: 5px;
    }

    .user-option:hover {
        background-color: #f0f0f0;
    }
